import React from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';

import articleContent from './article-content';

const StyledLink = styled(Link)`
    text-decoration: none;
    color: #1d3557;
    animation: text 2s 1;

    h3 {
        text-align:center;
    }

    p {
        display:inline-block;
        text-align:left;
        padding: 0px 5vw;
        padding-bottom: 40px;
        border-bottom: 2px solid grey;
    }

    @keyframes text {
        0%{
            color:#f1faee;
            margin-top: -40px;
        }
    }
`;

const StyledArticleList = styled.div`
    width: 50%;
    margin-left: auto;
    margin-right: auto;

    @media (max-width: 768px) {
        width: 100%;
    }

    @media (min-width: 1200px) {
        width: 40%;
    }
`;

const Blog = () => {
    const articles = articleContent;

    return (
    <StyledArticleList>
        {articles.map((article, key) => (
        <StyledLink key={key} to={`/blog-post/${article.name}`}>
            <h3>{article.title}</h3>
            <h5 style={{textAlign:"left", display:"inline-block", padding:"0px 30px"}}>{article.date}</h5>
            <h5 style={{textAlign:"right", display:"inline-block", float:"right", padding:"0px 30px"}}>{article.writer}</h5>
            <p>{article.content[0].substring(0, 250)}...</p>
        </StyledLink>
        ))}
    </StyledArticleList>
)};

export default Blog;
